<template>
  <view class="check-maint-record padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="维修检定"></u-navbar>
    <view>
      <nnkjc-detail-group>
        <nnkjc-detail-item
            detailTitle="维修检定"
            :align="'left'"
            :gap="false"
        >
          <view slot="content">
            <u-input type="textarea"
                     :height="200"
                     :maxlength="500"
                     placeholder="请填写维修检定结论（含技术参数及功能）"
                     placeholder-style="color: #D4D4D5; line-height: 1; font-size: 14px;"
                     v-model.trim="form.verifyContent"/>
          </view>
        </nnkjc-detail-item>

        <nnkjc-detail-item
            detailTitle="检定结果"
            :align="'left'"
            :gap="false"
        >
          <view slot="content">
            <u-radio-group v-model="form.passFlag">
              <u-radio
                  @change="resultChange"
                  :label-size="'14px'"
                  :active-color="'#24a27c'"
                  v-for="(res, index) in passList" :key="index"
                  :name="res.name"
              >
                {{res.title}}
              </u-radio>
            </u-radio-group>
          </view>
        </nnkjc-detail-item>
      </nnkjc-detail-group>
    </view>
    <view class="margin-normal radius-normal">
      <u-button
          ripple
          type="success"
          :loading="loading"
          @click="$u.debounce(submitForm, 500)">确认提交
      </u-button>
    </view>

    <u-toast ref="uToast"/>
  </view>
</template>

<script>

  export default {
    name: "check-maint-record",
    data() {
      return {
        loading: false,
        background: {
          'background': '#f2f2f8'
        },
        fileList: [],
        form: {
          id: "",
          type: "",
          passFlag: "1",
          verifyContent: ""
        },
        passList: [
          {
            title: "通过",
            name: "1"
          },
          {
            title: "不通过",
            name: "0"
          }
        ],
        imageUrl: []
      }
    },
    onLoad(options) {
      this.form.id = options.taskId;
      this.form.type = options.type;
    },
    methods: {
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      resultChange() {},
      submitForm() {
        if(this.imageUrl.length) {
          this.form.maintPic = this.imageUrl.join(',');
        }
        const finalForm = Object.assign({}, this.form);
        this.loading = true;
        this.$u
            .post('/maint/verifyTask', finalForm)
            .then(res => {
              if (res.code === 200) {
                this.$refs.uToast.show({
                  title: res.message,
                  type: 'success'
                });
                setTimeout(() => {
                  uni.reLaunch({
                    url: "/pages/home/index"
                  });
                }, 300)
              }
            }).catch(e => {
        }).finally(_ => {
          this.loading = false;
          this.$store.commit("freshTaskFunc", false);
        })
      },
    }
  }
</script>

<style scoped lang="scss">
  .add-btn {
    margin-top: 10px;
    width: 50px;
    height: 50px;
    border-radius: 5px;
    border: 1px dashed #f1f1f1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>